import React from 'react';
import { Route, Switch } from 'react-router-dom';

function App() {
	return (
		<div className="App">
			<Switch>
				<Route path="/" exact render={() => <h1>Home</h1>} />
				<Route path="/read/:id" exact render={() => <h1>Read</h1>} />
				<Route path="/compose" exact render={() => <h1>Compose</h1>} />
				<Route path="/" render={() => <h1>404</h1>} />
			</Switch>
		</div>
	);
}

export default App;
